<template>
    <div id="expressfeeevaluation">
        <div class="bodystyle">  
        </div>
        <div class="textstyle">
            <h1 class="text-center font-weight-bold"></h1>
            <p class="text-center font-weight-bold"></p>
        </div>
         <div class="border formstyle">
                <div class="pt-4 pl-4 pr-4">
                    <p class="text-center"><span style="color:red">* </span></p>
                    <div class="row">  
                                <div class="col-xl-4 col-lg-4 col-md-4 mt-3">
                                    <div class="row" style="margin:0 auto;">
                                        <div class="mt-1" style="width:110px;"> <span style="color:red">*</span></div>
                                        <div>
                                            <el-select style="width:170px;" v-model="evaluationForm.deliverRegion" placeholder="">
                                                <el-option label="" :value="1"></el-option>
                                            </el-select>
                                        </div>
                                    </div> 
                                </div>
                                <div class="col-xl-4 col-lg-4 col-md-4 mt-3">
                                    <div class="row" style="margin:0 auto;">
                                        <div class="mt-1" style="width:110px;">(kg) <span style="color:red">*</span></div>
                                        <div ><el-select style="width:170px;" v-model="evaluationForm.goodWeight">
                                                <el-option
                                                    v-for="(item, index) in 200"
                                                    :key="index"
                                                    :label="item"
                                                    :value="item">
                                                </el-option>
                                            </el-select></div>
                                    </div> 
                                </div>
                                <div class="col-xl-4 col-lg-4 col-md-4 col-12 mt-3">
                                    <div class="row" style="margin:0 auto;">
                                        <div class="mt-1" style="width:110px;"> <span style="color:red">*</span></div>
                                        <div ><el-select style="width:170px;" v-model="evaluationForm.goodType" placeholder="">
                                                <el-option
                                                    v-for="(item, index) in goodsTypeOptions"
                                                    :key="index"
                                                    :label="item.label"
                                                    :value="item.value">
                                                </el-option>
                                            </el-select>
                                        </div>
                                    </div> 
                                </div>
                    </div>
                    <div class="row" style="margin:0 auto">
                                <div class="row mt-3 no-gutters">
                                    <div style="width:140px;" class="mt-2">
                                        (cm) <span style="color:red">*</span>
                                    </div>
                                    <el-row class="col" style="width:100%">
                                            <el-col :xl="7" :lg="7" :md="7" :sm="18" :xs="20"  class="mt-1">
                                                <el-row>
                                                    <el-col :xl="4" :lg="4" :md="6" :sm="6" :xs="6" class="mt-1"></el-col>
                                                    <el-col :xl="19" :lg="18" :md="16" :sm="14" :xs="14"><el-input-number v-model="evaluationForm.goodLength" controls-position="right" ></el-input-number></el-col>
                                                </el-row>
                                            </el-col>
                                            <el-col :xl="7" :lg="7" :md="7"  :sm="18" :xs="20"  class="mt-1">
                                               <el-row>
                                                    <el-col :xl="4" :lg="4" :md="6" :sm="6" :xs="6" class="mt-1"></el-col>
                                                    <el-col :xl="19" :lg="18" :md="16" :sm="14" :xs="14"><el-input-number v-model="evaluationForm.goodWidth" controls-position="right" ></el-input-number></el-col>
                                                </el-row>
                                            </el-col>
                                            <el-col :xl="7" :lg="7" :md="7" :sm="18" :xs="20" class="mt-1">
                                                <el-row>
                                                    <el-col :xl="4" :lg="4" :md="6" :sm="6" :xs="6" class="mt-1"></el-col>
                                                    <el-col :xl="19" :lg="18" :md="16" :sm="14" :xs="14"><el-input-number v-model="evaluationForm.goodHeight" controls-position="right" ></el-input-number></el-col>
                                                </el-row>
                                            </el-col>
                                            <el-col :xl="3" :lg="3" :md="3" :sm="3" :xs="3" class="mt-2">
                                                <el-popover
                                                    placement="right"
                                                    width="380"
                                                    trigger="hover">
                                                    <div>
                                                        <div>
                                                            <i class="fas fa-check mr-1 text-danger"></i>
                                                        </div>
                                                        <div>
                                                            <i class="fas fa-check mr-1 text-danger"></i>(kg) = cm * cm * cm / 5000
                                                        </div>
                                                    </div>
                                                    <i slot="reference" class="fas fa-question-circle text-warning ml-1" style="font-size:23px;cursor:pointer;"></i>
                                                </el-popover>  
                                            </el-col>
                                    </el-row>
                                </div>
                    </div>
                </div>
                    
                <div style="margin-top:30px;margin-bottom:20px;" class="text-center">
                    <el-button style="width:160px;background-color:#212537;color:white" @click="showFeeEvaluation"><span style="font-size:18px;"> <i class="fas fa-search"></i></span> </el-button>
                </div>
                <el-collapse-transition>
                    <div class="pl-4 pr-4 mt-4" style="margin-bottom:20px;" v-if="searchSign">
                        <el-table
                            :data="expressInfo"
                            style="width: 100%;font-size:14px;color:black;">
                            <el-table-column
                            label=""
                            prop="label"
                            width="100"
                            align="center"
                            >
                            <template slot-scope="{row}">
                                <img class="pt-1 pb-1" :src="row.icon" style="width:80%;">
                            </template>
                            </el-table-column>
                            <el-table-column
                            label="(kg)"
                             width="140"
                             align="center"
                            >
                            <template slot-scope="{row}">
                                <span class="text-center">
                                     {{ row.realWeight  }} 
                                </span>
                            </template>
                            </el-table-column>
                            <el-table-column
                            label="()"
                            width="150"
                            prop="firstAmount"
                            align="center"
                            sortable
                            >
                            <template slot-scope="{row}">
                                <span><i class="fas fa-yen-sign mr-2 text-danger" style="font-size:20px;"></i>{{ row.firstAmount  }} / 500g</span>
                            </template>
                            </el-table-column>
                            <el-table-column
                            label="()"
                            width="150"
                            prop="secondAmount"
                            align="center"
                            sortable
                            >
                            <template slot-scope="{row}">
                                <span><i class="fas fa-yen-sign mr-2 text-danger" style="font-size:20px;"></i>{{ row.secondAmount  }} / 500g</span>
                            </template>
                            </el-table-column>
                            <el-table-column
                            label="()"
                            width="140"
                            prop="totalAmount"
                            align="center"
                            sortable>
                            <template slot-scope="{row}">
                                <span><i class="fas fa-yen-sign mr-2 text-danger" style="font-size:20px;"></i>{{ row.totalAmount  }}</span>
                            </template>
                            </el-table-column>
                            <el-table-column
                            label="()"
                            width="180"
                            prop="deliveryTime"
                            align="center"
                            sortable>
                            <template slot-scope="{row}">
                                <span><i class="far fa-calendar-alt mr-2 text-success" style="font-size:20px;"></i>{{ row.deliveryTime  }}</span>
                            </template>
                            </el-table-column>
                            <el-table-column
                            label=""
                            align="center"
                            >
                            <template slot-scope="{row}">
                                <span style="color:gray;font-size:12px;">{{ row.note  }}</span>
                            </template>
                            </el-table-column>
                        </el-table>
                        <div>
                            <p class="text-danger text-center mt-4"><i class="fas fa-exclamation-circle"> </i> </p>
                            <p>1</p>
                            <p>2</p>
                            <p>3</p>
                            <p>4</p>
                            <p>5</p>
                            <p  style="cursor:pointer" @click="dialogVisible = true">6
                                <span><b> : </b> MUAMUAYVETTE</span>
                                <i class="fas fa-search ml-1"></i>
                            </p>
                        </div>
                        <div class="text-center mt-4">
                            <el-button @click="searchSign=false"></el-button>
                        </div>
                    </div>
                </el-collapse-transition>
            </div>
            <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="400px"
            >
                <img
                    class="mx-auto d-block"
                    src="/images/weixinCode.png"
                />
            </el-dialog>
        </div>               
</template>

<script>
import { ref, reactive, computed } from '@vue/composition-api';
import {calculateFee} from '../../../utils/function';
export default {
    setup(props, {root:{$message}}){
        const searchSign = ref(false);
        
        const dialogVisible = ref(false);

        const expressOptions = reactive([
            {
                label:"DHL",
                value:"1",
                time:"10",
                note: "",
                icon: '/images/ExpressIcon/dhl.png'
            },
            {
                label:"UPS",
                value:"2",
                time:"11",
                note: "",
                icon: "/images/ExpressIcon/ups.png",
            },
            {
                label:"FedEx",
                value:"3",
                time:"12",
                note: "",
                icon: "/images/ExpressIcon/fedex.png",
            },
            {
                label:"TNT",
                value:"4",
                time:"13",
                note: "",
                icon: "/images/ExpressIcon/tnt.png",
            },
            {
                label:"EMS",
                value:"5",
                time:"14",
                note: "",
                icon: "/images/ExpressIcon/ems.png",
            },
            {
                label:"Andy",
                value:"6",
                time:"15",
                note: "",
                icon: "/images/ExpressIcon/special.png",
            },
        ]);

        const goodsTypeOptions = reactive([
            {
                label:"",
                value:"0",
            },
            {
                label:"",
                value:"1",
            },
            {
                label:"",
                value:"2",
            },
            {
                label:"",
                value:"3",
            },
            {
                label:"",
                value:"4",
            },
            {
                label:"",
                value:"5",
            },
        ]);

        const evaluationForm = reactive({
            deliverRegion: '',
            goodWeight: 1,
            goodType:'',
            goodLength:1,
            goodWidth:1,
            goodHeight:1,
        });

        const tableData = ref([]);

        const showFeeEvaluation = () =>{
            if(evaluationForm.deliverRegion ==='' || evaluationForm.goodType ===''){
                $message.error("");
            }
            else{
                tableData.value = calculateFee(parseInt(evaluationForm.goodWeight), parseInt(evaluationForm.goodType), 
                parseInt(evaluationForm.goodLength), parseInt(evaluationForm.goodWidth), parseInt(evaluationForm.goodHeight), expressOptions);
                searchSign.value = true;
            }
            
        };

        const expressInfo = computed(()=>{
            return tableData.value;
        })

        return{
            searchSign,
            showFeeEvaluation,
            expressOptions,
            goodsTypeOptions,
            evaluationForm,
            expressInfo,
            dialogVisible 
        }
    }
}
</script>

<style lang="scss" scoped>
.bodystyle{
       padding-bottom: 100px;
       width:100%;
       margin:0 auto;
       background-image:url(/images/FeeEvalBG.png);
       background-size: 100% 100%;
       z-index:-1;
       height:300px;
       top:0;
       opacity: 0.5;
}
.textstyle{
     z-index:2;
     opacity: 0.99;
     margin-top:-220px;
     font-weight:700;
}
.formstyle{
    box-shadow: 4px 4px 5px 0px rgba(0,0,0,0.75);
    width:70%;
    margin:0 auto;
    z-index:2;
    margin-top:60px;
    background:rgba(255,255,255,1);
    opacity: 0.99;
    margin-bottom:40px;
}

.input-width{
    width:calc(100%-110px);
}
.el-input-number--small {
    width: 100%;
}

@media only screen and (max-width: 576px) {
  .formstyle {
    width:95%;
  }
}

    
</style>